/* Style the tabe */
.tabe {
    overflow: hidden;
    background-color: #f1f1f1;
    text-align: center;
    display: grid;
    grid-template-columns: 3fr 3fr 3fr 3fr;
  }
  
  /* Style the buttons that are used to open the tabe content */
  .tabe button {
    background-color: inherit;
    float: left;
    width: 100%;
    border: 1px solid gainsboro;
    outline: none;
    cursor: pointer;
    justify-self: center;
    font-size: 14px;
    font-weight: bold;
    color: #2e2e2e;
    padding: 10px 8px;
    -webkit-transition: 0.3s;
    transition: 0.3s;
  }

  @media only screen and (max-width : 321px){
    .tabe button {
        font-size: 10px;
    }
  }
  
  /* Change background color of buttons on hover */
  .tabe button:hover {
    background-color: #ddd;
  }
  
  /* Create an active/current tabelink class */
  .tabe button.active {
    background-color: white;
    border-bottom: 5px solid  #2CC185;
    color: #2CC185;
  }
  
  /* Style the tabe content */
  .tabecontent {
    display: none;
    padding: 6px 12px;
    border-top: none;
  }

  .tabecontent {
    -webkit-animation: fadeEffect 1s;
            animation: fadeEffect 1s; /* Fading effect takes 1 second */
  }
  
  /* Go from zero to full opacity */
  @-webkit-keyframes fadeEffect {
    from {opacity: 0;}
    to {opacity: 1;}
  }
  @keyframes fadeEffect {
    from {opacity: 0;}
    to {opacity: 1;}
  }

  /* outra tab */

  .invisible{
    display: none;
  }
  ._tab{
    overflow: hidden;
    background-color: #f1f1f1;
    text-align: center;
  }
  .tam4{
    display: grid;
    grid-template-columns: 3fr 3fr 3fr 3fr;
  }
  .tam1{
    display: grid;
    grid-template-columns: 12fr;
  }
  .tam2{
    display: grid;
    grid-template-columns: 6fr 6fr;
  }
  .tam3{
    display: grid;
    grid-template-columns: 4fr 4fr 4fr;
  }
  ._tabelinha{
    overflow: hidden;
    background-color: #f1f1f1;
    text-align: center;
    display: grid;
    grid-template-columns: 4fr 4fr 4fr;
  }
  
  .mais .subtabela{
    display: none;
  }
  .mais button{
    background-color: #2CC185;
    color: white;
    padding: 10px 40px 10px 40px;
    border: 1px solid whitesmoke;
    border-radius: 5px;
    cursor: pointer;
    -webkit-transition: 0.1s;
    transition: 0.1s;
  }

  .mais button:hover{
    border: 1px solid #2CC185;
    background-color:white;
    color: #2CC185;
  }
  .none{
    display: none;
  }
